<template>
  <div class="roles">
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮区 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click='addRoleDesc=true'>添加角色</el-button>
        </el-col>
      </el-row>

      <!-- 角色列表区 -->
      <el-table :data="roleList" style="width: 100%" border stripe>
          <el-table-column type='expand' label="展列" width='100'>
              <template slot-scope="scope">
                  <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' : '','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                      <!-- 一级权限 -->
                      <el-col :span="5">
                          <el-tag closable @close='removeRightById(scope.row,item1.id)'>
                              {{ item1.authName }}
                          </el-tag>
                          <i class='el-icon-caret-right'></i>
                      </el-col>
                      <!-- 二级权限和三级权限 -->
                       <el-col :span="19">
                             <el-row :class="[i2 === 0 ? '' :'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id" >
                                 <el-col :span='6'>
                                     <el-tag type='success' closable @close='removeRightById(scope.row,item2.id)'>{{ item2.authName }}</el-tag>
                                      <i class='el-icon-caret-right'></i>
                                 </el-col>
                                  <el-col :span='18'>
                                      <el-tag type="warning" v-for="(item3,i3) in item2.children" closable :key="item3.id"  @close='removeRightById(scope.row,item3.id)'>
                                               {{ item3.authName }}
                                      </el-tag>
                                  </el-col>
                             </el-row>
                       </el-col>
                  </el-row>
                  <!-- <pre>
                      {{ scope.row }}
                  </pre> -->
              </template>
          </el-table-column>
          <!-- 索引列 -->
        <el-table-column type="index" label="序号" width='100'></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column  label="操作">
            <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click='editDesclog(scope.row.id)' size='mini'>编辑</el-button>
                <el-button type="danger" icon="el-icon-delete"  size='mini' @click='editRemove(scope.row.id)'>删除</el-button>
                <el-button type="warning" icon="el-icon-setting"  size='mini' width='200px' @click='showDesc(scope.row)'>分配权限</el-button>
            </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 修改对话框 -->
<el-dialog
  title="修改角色"
  :visible.sync="editDescdialog"
  width="50%"
  @close='editdescClose'
  >
  <!-- 修改用户信息区域表单 -->
   <el-form :model="editDescs" ref ='editDescRef' label-width="70px" >
  <el-form-item label="角色名称">
    <el-input v-model="editDescs.roleName" ></el-input>
  </el-form-item>
  <el-form-item label="角色描述">
    <el-input v-model="editDescs.roleDesc" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDescdialog = false">取 消</el-button>
    <el-button type="primary" @click="editDescInfo">确 定</el-button>
  </span>
</el-dialog>
<!-- 添加角色对话框 -->
 <el-dialog
  title="添加角色"
  :visible.sync="addRoleDesc"
  width="50%"
  @close='addDescClose'
  >
  <!-- 内容主体区 -->
   <el-form :model="addDesc" :rules="addFormRules"  ref = 'addDescRef' label-width="70px" >
  <el-form-item label="角色名称">
    <el-input v-model="addDesc.roleName"></el-input>
  </el-form-item>
   <el-form-item label="角色描述">
    <el-input v-model="addDesc.roleDesc" ></el-input>
    </el-form-item>
  </el-form>
  <!-- 底部区域 -->
  <span slot="footer" class="dialog-footer">
    <el-button @click="addRoleDesc = false">取 消</el-button>
    <el-button type="primary" @click="addroleDesc">确 定</el-button>
  </span>
</el-dialog>

<!-- 添加对话框结束 -->
<!-- 分配权限对话框 -->
     <el-dialog
  title="分配权限"
  :visible.sync="showRight"
  width="30%"
  @close='setRightDialogClose'
  >
  <!-- 树形权限 -->
  <el-tree :data="rightList" :props="treeProps" ref="treeRef" show-checkbox node-key='id' default-expand-all :default-checked-keys='defkeys'></el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="showRight = false">取 消</el-button>
    <el-button type="primary" @click="allotRights">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      //所有角色列表数据
      roleList: [],
      editDescs:{ //修改参数对象
          roleName:'',
          roleDesc:''
      },
      addDesc:{  //添加参数对象
          roleName:'',
          roleDesc:''
      },
      editDescdialog:false,//修改角色状态
      addRoleDesc:false,//添加角色状态
      showRight:false,//分配权限状态
      rightList:[],//权限数据集合
    //   树形控件
    treeProps:{
        label:'authName',
        children:'children'
    },
    //默认选中的权限数组
    defkeys:[],
    roleId:''//当前即将分配权限的角色ID
    }
  },
  watch: {},
  computed: {},
  methods: {
      //获取角色列表数据
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      this.roleList = res.data
      console.log(this.roleList)
    },
    //监听角色添加关闭事件
    addDescClose(){
     this.$refs.addDescRef.resetFields()
    },
    //点击确认按钮，添加角色
    addroleDesc(){
        this.$refs.addDescRef.validate(async valid => {
            if(!valid) return 
            //发起添加用户的网络请求
           const { data:res } = await this.$http.post('roles',this.addDesc)
           if(res.meta.status !== 201){
              this.$message.error('添加角色失败')
           }
            this.$message.success('添加角色成功')
            this.addRoleDesc = false
            //重新获取用户列表数据
            this.getRoleList()
        })
    },
    //修改用户信息
    async editDesclog(id){
        this.editDescdialog = true
      const { data:res } = await this.$http.get('roles/' + id)
      if(res.meta.status !== 200){
          return this.$message.error('修改用户信息失败')
      }
      this.editDescs = res.data
    },
      //监听用户关闭修改
    editdescClose(){
        this.$refs.editDescRef.resetFields()
    },
    //修改用户并提交
     editDescInfo(){
        this.$refs.editDescRef.validate( async valid =>{
            console.log(valid)
            if(!valid) return 
            //发起添加用户的网络请求
           const { data:res } = await this.$http.put('roles/'+this.editDescs.roleId,{
               roleName:this.editDescs.roleName,
               roleDesc:this.editDescs.roleDesc
           })
           if(res.meta.status !== 200){
              return this.$message.error('修改角色信息失败')
           }
            //关闭修改对话框
            this.editDescdialog = false
            this.$message.success('修改角色信息成功')
             this.getRoleList()//重新获取角色列表数据
        })
    },
      //根据ID删除对应用户信息
    async editRemove(id){
        //弹框询问用户是否删除数据
      const Reault = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        // 点击取消则返回字符串cancel
          if(Reault !== 'confirm'){
              return this.$message.info('已取消删除')
          }
          const {data:res } = await this.$http.delete('roles/' + id)
          if(res.meta.status !== 200){
               return this.$message.error('删除用户失败')
          }
          this.$message.success('删除用户成功')
          this.getRoleList()
    },
    //根据Id删除对应的权限
   async removeRightById(role,rightId){
        const confirmResult = await this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        if(confirmResult !== 'confirm'){
             return this.$message.info('已取消删除')
        }
        // console.log(确认了删除)
       const { data:res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if(res.meta.status !== 200){
               return this.$message.error('删除权限失败')
          }
          this.$message.success('删除权限成功')
        //   this.getRoleList()
          role.children = res.data
    },
    //分配权限
    async showDesc(role){
        this.roleId = role.id
        const { data:res } = await this.$http.get('rights/tree')
        if(res.meta.status !== 200){
            return this.$message.error('获取权限数据失败')
        }
        //获取到的权限数据
        this.rightList = res.data
        console.log(this.rightList)
        //获取递归到角色的权限
        this.getLeafKeys(role,this.defkeys)
        //弹出对话框
        this.showRight = true

    },
    //通过递归的函数获取角色下所有三级权限的id，并保存在defkeys数组中
    getLeafKeys(node,arr){
        if(!node.children){
            return arr.push(node.id)
        }
        node.children.forEach(item =>this.getLeafKeys(item,arr))
    },
    // 监听分配权限的关闭事件
    setRightDialogClose(){
        this.defkeys = []
    },
    //为角色分配权限
   async allotRights(){
        const keys = [
            ...this.$refs.treeRef.getCheckedKeys(),
             ...this.$refs.treeRef.getHalfCheckedKeys(),
        ]
        const idStr = keys.join(',')
       const { data:res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
       if(res.meta.status !== 200){
             return this.$message.error('分配权限失败')
       }
         this.$message.success('分配权限成功')
         this.getRoleList()
         this.showRight = false
    }

  },
  created() {
    this.getRoleList()
  },
  mounted() {},
}
</script>
<style lang="less" scoped>
.el-tag{
    margin:7px;
}
.bdtop{
    border-top:1px solid #eee;
}
.bdbottom{
    border-bottom:1px solid #eee;
}
.vcenter{
    display:flex;
    align-items:center;
}
</style>