<template>
  <div class="fby_login">
    <div class="fby_login_box">
      <!-- 头像区 -->
      <div class="fby_avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!-- 登录表单区域 -->
      <el-form ref='Loginref' :model='Loginform' :rules="Loginrules" label-width="0px" class='fby_login_form'>
          <!-- 用户名 -->
        <el-form-item prop='username'>
          <el-input v-model='Loginform.username' prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop='password'>
          <el-input type='password' v-model='Loginform.password' prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <!-- button按钮区域 -->
        <el-form-item class='fby_btns'>
          <el-button type="primary" @click='Loginvalidate'>登录</el-button>
           <el-button type="info" @click='Loginreset'>重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        //登录表单的数据绑定对象
         Loginform: {
          username: 'admin',
          password: '123456'
        },
        Loginrules:{
            //验证用户名
            username: [
            { required: true, message: '请输入登录用户名', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入登录密码', trigger: 'blur' },
            { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
          ],
        }
    };
  },
  watch: {},
  computed: {},
  methods: {
    //重置按钮函数
    Loginreset(){
        this.$refs.Loginref.resetFields()
    },
    //登录表单预验证
     Loginvalidate(){
        this.$refs.Loginref.validate(async valid =>{
            if(!valid) return;
            const { data:res } = await this.$http.post('login',this.Loginform)
            if(res.meta.status !== 200)return this.$message.error('登录失败')
             this.$message.success('登录成功')
             console.log(res)
             //1.将登陆成功后的token存储到客户端的sessionStorage中
            //  1.1项目中出了登录之外的其他API接口，必须在登陆之后才能访问
            // 1.2token值应在当前网站打开期间生效，所以将token保存在sessionStorage中
             window.sessionStorage.setItem('token',res.data.token)
             //2.通过编程式导航跳转到后台主页，路由地址为/home
             this.$router.push('/home')
        })
    }
  },
  created() {},
  mounted() {},
};
</script>
<style lang="less" scoped>
.fby_login {
  background: #2b4b6b;
  height: 100%;
}
.fby_login_box {
  width: 450px;
  height: 300px;
  background: #fff;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .fby_avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    background: #fff;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: #eee;
    }
  }
}
.fby_login_form{
    position:absolute;
    bottom:0;
    width:100%;
    padding:0 20px;
    box-sizing: border-box;
}
.fby_btns{
    display:flex;
    justify-content: flex-end;
}
</style>